import { Component, OnInit} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { NavparamService } from '../navparam.service';


import 'leaflet/dist/leaflet.css';
import * as Leaflet from 'leaflet'; 
import {Map, tileLayer, marker} from 'leaflet';

import "esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css";
import "esri-leaflet-geocoder/dist/esri-leaflet-geocoder";

@Component({
  selector: 'app-order-detail',
  templateUrl: './order-detail.page.html',
  styleUrls: ['./order-detail.page.scss'],
})
export class OrderDetailPage implements OnInit {

  map: Leaflet.Map;
  properties = [];

  deliver = false;
  acc=true;
  dec=true;
  ship=false;

  datastorage: any;
  fname : any;
  lname : any;
  street: any;
  barangay: any;
  brgy: any;
  bid: any=0;
  number: any;
  id: any=0;
  orders: any = [];
  oid = this.navParamService.getOrderID();;
  specific: any;
  lattitude: any = 0;
  longitude: any = 0; 
  user: any;
  did:any;
  total:any;
  con: any;

  
  constructor(   
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private activatedRoute: ActivatedRoute,
    private storage: Storage,
    private navParamService: NavparamService
  ) { }


  ngOnInit() {
    this.con=this.navParamService.getCon();
    console.log(this.con);
    if(this.con=='Picked-up by courier'){
      this.acc=false;
      this.dec=false;
      this.ship=true;
    }if(this.con=='Shipped by courier'){
      this.deliver=true;
      this.acc=false;
      this.dec=false;
      this.ship=false;
       
    }
    console.log(this.oid);  
    this.orders=[];
    return new Promise(resolve => {
      let body = {
        aksi: 'get_orders_details',
        oid: this.oid,
      }
      console.log(this.oid);
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
          console.log(res.user)
          console.log(res.result)
          this.user=res.user;
          this.specific=this.user.specific;
          parseFloat(this.longitude=this.user.longitude);
          parseFloat(this.lattitude=this.user.lattitude);
          console.log(this.lattitude);
          console.log(this.longitude);
          this.fname=this.user.first_name;
          this.lname=this.user.last_name;
          this.street=this.user.street;
          this.barangay=this.user.barangay_name;
          this.brgy=this.user.barangay_name;
          this.number=this.user.contact_number;
          this.total=res.total;
          for(let datas of res.result){
             this.orders.push(datas);
           }
           console.log(this.orders);

        } else {
          console.log('x');
        }
      });
    });
   
  }
    
  leafletMap() {
    this.map = new Leaflet.Map('map').setView([14.0221, 120.7284], 14);

    Leaflet.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {attribution: 'Tuy-Pocket Market',
    }).addTo(this.map);

    Leaflet.marker([this.lattitude, this.longitude]).addTo(this.map).bindPopup('Order Destination').openPopup();

    //const markPoint = Leaflet.marker([14.0221, 120.7284]);
   // markPoint.bindPopup('<p>Order Destination.</p>');
   // this.map.addLayer(markPoint);
   
    
    console.log('map working');
  } 

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }

  accept(){
    this.ship=true;
    this.acc=false;
    this.dec=false;
    console.log(this.number);
        return new Promise(resolve =>{
      console.log(this.oid);
      let body = {
        aksi: 'accept_order',
        num: this.number,
        orderid: this.oid, 
        fname: this.fname,
        did: this.did,
        bid: this.bid
      }
      this.accessProviders.postData(body, 'sms.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.msg);
        } else {
          this.presentToast('Failed'); 
          this.router.navigate(['/panel-driver/1'])
        }
      });   
    });
 
  }
  decline(){
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.barangay=this.datastorage.barangay_name;

    console.log(this.number);
        return new Promise(resolve =>{
      console.log(this.oid);
      let body = {
        aksi: 'decline_order',
        num: this.number,
        orderid: this.oid,
        fname: this.fname,
        did: this.did,
        bid: this.bid,
        barangay: this.barangay,
        brgy: this.brgy
      }
      console.log(this.barangay);
      this.accessProviders.postData(body, 'sms.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.result);
           this.router.navigate(['/panel-driver/1'])
        } else {
          this.presentToast('Failed'); 
        }
        });   
      });
    });
  }
  shipment(){
    this.deliver=true;
    this.ship=false;
    console.log(this.number);
        return new Promise(resolve =>{
      console.log(this.oid);
      let body = {
        aksi: 'ship_order',
        num: this.number,
        orderid: this.oid,
        fname: this.fname,
        did: this.did,
        bid: this.bid
      }
      this.accessProviders.postData(body, 'sms.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.result);
           this.con='Shipped by courier';
           this.navParamService.setCon(this.con);
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });
    
  }
  
  delivered(){
    this.acc=false;
    this.dec=false;
    console.log(this.number);
        return new Promise(resolve =>{
      console.log(this.oid);
      let body = {
        aksi: 'order_delivered',
        num: this.number,
        orderid: this.oid,
        fname: this.fname,
        did: this.did,
        bid: this.bid
      }
      this.accessProviders.postData(body, 'sms.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.result);
           this.router.navigate(['/panel-driver/1'])
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });
  }
  ionViewDidEnter(){
    this.leafletMap();
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.did = this.datastorage.user_id;
        this.bid = this.datastorage.barangay_id;
    });
     
  }

  back(){
    this.router.navigate(['panel-driver/1']);
  }
  async presentAlert(a) {
    const alert = await this.alertController.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Okay',
          handler: () => {
            console.log('Confirm Okay');
          }
        }
      ]
    });
  }
}
